/* Overlay pleine largeur/hauteur */
.custom-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    cursor: pointer;
  }
  
  .modal-content {
    position: relative;
    background: #1c1d1f;
    border-radius: 8px;
    width: 90%;
    max-width: 600px;
    max-height: 90%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    border: 2px solid #c59d6b;
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background: #1c1d1f;
    color: #ffffff;
    border-bottom: 1px solid #c59d6b;
  }
  
  .modal-title {
    font-size: 1.5rem;
    margin: 0;
  }
  
  .modal-close {
    background: none;
    border: none;
    color: #ffffff;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .modal-body {
    padding: 15px;
    flex: 1;
    overflow-y: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .modal-images {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .modal-image {
    max-width: 100%;
    border-radius: 4px;
    border: 1px solid #c59d6b;
  }
  
  .modal-footer {
    display: flex;
    justify-content: space-between;
    padding: 15px;
    background: #1c1d1f;
    border-top: 1px solid #c59d6b;
  }
  
  .modal-button {
    padding: 10px 20px;
    font-size: 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    text-decoration: none;
    text-align: center;
  }
  
  .action-button {
    background-color: #c59d6b;
    color: #ffffff;
  }
  
  .action-button:hover {
    background-color: #a57f4f;
  }
  
  .download-button {
    background-color: transparent;
    color: #c59d6b;
    border: 1px solid #c59d6b;
  }
  
  .download-button:hover {
    background-color: #c59d6b;
    color: #ffffff;
  }